#audio-player {
    @include flex-center;
    flex-direction: column;
    position: fixed;
    bottom: 50%;
    right: 0;
    max-width: 25vw;
    color: black;
    transition: transform 0.5s ease-out;
    &.hidden {
        transform: translateX(100%); } }

#current-song-title {
    text-align: center;
    font-size: 1.3em;
    color: $deep-magenta;
    letter-spacing: 1px;
    padding: 10px;
    background: $white;
    border: solid 2px $deep-magenta;
    border-right-color: transparent;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    width: 100%;
    display: block; }


#audio-player-controls {
    padding: 15px;
    text-align: center;
    button {
        padding: 10px;
        border-radius: 10px;
        border: 0;
        box-shadow: 2px 3px 5px 0 rgba(0,0,0,0.7);
        background: $white;
        border: solid 1px $deep-magenta;
        color: $deep-magenta;
        cursor: pointer;
        &:focus {
            outline-width: 0; } }
    #play-btn {
        border-radius: 50%;
        padding: 20px;
        font-size: 1.2em; } }
